@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background: #FBFBFB;
}

.Content {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: grey;
}

.Auth-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Auth-item {
  margin: 2em 0 2em 0;
}

label {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 42px;
  text-align: center;
  color: #000000;
}

input {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  outline: 0;
    width: 330px;
  border-width: 0 0 2px;
  border-color: lightgray
}

button {
  width: 94px;
  height: 36px;
  background: #009688;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24), 0px 0px 2px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
  color: white;

  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.0357143em;
}
/* TOPBAR */
.TopBar-container {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  justify-content: space-between;
  background-color: white;
  height: 64px;
}

.TopBar-item {
  margin: auto; /* Magic! */
}

button {
  background: #000000;
  border-radius: 5px;
  width: 140px;
  height: 40px;
  -webkit-transition: background 0.1s;
  transition: background 0.1s;
}

button:hover {
  background:#444444;
  -webkit-transition: background 0.2s;
  transition: background 0.2s;
}

h1 {
  font-size: 24px;
  line-height: 28px;
}

h2 {
  font-size: 18px;
  line-height: 21px;
}
.rowTd {
  border: 1px solid #ddd;
  color: black;
  background: #f9f9f9;
}

.popup {  
  position: fixed;  
  width: 100%;  
  height: 100%;  
  top: 0;  
  left: 0;  
  right: 0;  
  bottom: 0;  
  margin: auto;  
  background-color: rgba(0,0,0, 0.5);  
}  
.popup\_inner {  
  position: absolute;  
  left: 25%;  
  right: 25%;  
  top: 25%;  
  bottom: 25%;  
  margin: auto;  
  border-radius: 20px;  
  background: white;  
}
/* TABLE */
.Actions-List {
  border-collapse: collapse;
}
.Actions-tr td {
  color: black;
  background-color: white;
}

